<template>
	<view class="warp">
		<view class="inpt-content">
			<textarea v-model="content" class="inpt-text"></textarea>
		</view>
		
		<view class="inpt-imge">
			<uni-file-picker 
				:auto-upload="false"
				class="upload"
				v-model="imageValue"
				fileMediatype="image" 
				mode="grid" 
				:limit="6"
				@select="select" 
				@progress="progress" 
				@success="success" 
				@fail="fail" 
				@delete="remove"
				
			>
			<image style="width: 30px;height: 30px;" src="../../static/images/icon/pic.png"></image>
			</uni-file-picker>
		</view>
		
		<view @click="sead()" class="inpt-sead">
			<span v-if="loading==0">发送</span>
			<span v-else-if="loading==1">发送中</span>
			<span v-else>图片上传中</span>
			</view>
	</view>
</template>

<script>
	import {addDiscuss,uploadImage} from "@/api/addCard.js"
	export default {
		data() {
			return {
				topicId:null,
				content:"",
				loading:0,
				imageValue:[
					
					
				]
				// value:[
				// 	{
				// 		"name":"file.txt",
				// 		"extname":"txt",
				// 		"url":"https://xxxx",
				// 		// ...
				// 	}
				// ]

			}
		},
		onLoad(op) {
			this.topicId = op.topicId
		},
		methods: {
			// 获取上传状态
			async select(e){
				// console.log('选择文件：',e)
				// console.log(this.imageValue)
				e.tempFilePaths.forEach(file=>{
					const params ={
						name : "file",
						// filePath : e.tempFilePaths[0],
						filePath:file,
					}
					this.loading = 2
					uploadImage(params).then(res=>{
						this.loading = 0;
						this.imageValue.push({url:this.getUrl(res.url)})
					}).catch(err=>{
						this.loading = 0;
						// console.log("上传出错")
						// console.log(err)
					})
				})
			
			
			},
			// 获取上传进度
			progress(e){
				console.log('上传进度：',e)
				
			},
			
			// 上传成功
			success(e){
				console.log('上传成功')
			},
			
			// 上传失败
			fail(e){
				console.log('上传失败：',e)
			},
			//移除文件
			remove(e){
				console.log(this.imageValue)
			},
			sead(){
				if(this.loading!=0)
					return;
				console.log(this.imageValue)
				let pictrueList = []
				if(this.imageValue!=null&&this.imageValue.length>0)
					this.imageValue.forEach(item=>{
						pictrueList.push(item.url)
					})
				this.loading=1
				const params = {
					topicId:this.topicId,
					content:this.content,
					pictrueList:pictrueList
				}
				addDiscuss(params).then(res=>{
					console.log("成功！")
					this.loading=0
					uni.navigateBack();
				}).catch(err=>{
					this.loading=1
				})
			}
		}
	}
</script>

<style>
.inpt-text{
	width: calc( 100% - 20px );
	margin-top: 10px;
	padding: 10px;
	height: 500rpx;
	box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px, rgb(51, 51, 51) 0px 0px 0px 3px;
	border-radius: 10px;
}
.inpt-sead{
	width: 100%;
	height: 100rpx;
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: #fccd43;
	border-radius: 10px;
	
}
.inpt-imge{
	margin: 100rpx 0;
	
}
.uni-file-picker{


}
</style>
